
import React from 'react';
import {
    Container,
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    NavLink,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem 
} from 'reactstrap';

import './menubar.css';

import logo from './../../images/Logo.png';

export default class Example extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }
  render() {
    return (
      <Container>
        <Navbar color="white" light expand="md" className="navbar-inverse">
          
          <NavbarToggler onClick={this.toggle} />
          <Collapse className="col-md-6" isOpen={this.state.isOpen} navbar style={{paddingLeft: 0}}>
            <Nav navbar>
              <NavItem className="menu-item roboto-bold-font">
                <NavLink href="">WHO WE ARE</NavLink>
              </NavItem>
              <NavItem className="menu-item roboto-bold-font">
                <NavLink href="">GIVE</NavLink>
              </NavItem>
              <NavItem className="menu-item roboto-bold-font">
                <NavLink href="">PODCASTS</NavLink>
              </NavItem>
              <NavItem className="menu-item roboto-bold-font">
                <NavLink href="">CONTACT</NavLink>
              </NavItem>
              <NavItem className="menu-item roboto-bold-font collapsed-item-plan-visit">
                <NavLink href="">PLAN A VISIT</NavLink>
              </NavItem>
              {/* <UncontrolledDropdown nav inNavbar>
                <DropdownToggle nav caret>
                  Options
                </DropdownToggle>
                <DropdownMenu right>
                  <DropdownItem>
                    Option 1
                  </DropdownItem>
                  <DropdownItem>
                    Option 2
                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem>
                    Reset
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown> */}
            </Nav>
          </Collapse>
          <div className="nav-logo">
            <img src={logo} alt="logo" />
          </div>
          <div className="plan-visit-menu">
            <NavbarBrand className="menu-item item-plan-visit roboto-bold-font" href="/">PLAN A VISIT</NavbarBrand>
          </div>
        </Navbar>
      </Container>
    );
  }
}


