
import React, { Component } from 'react';
import {
    Container,
    Row,
    Col
} from 'reactstrap';

import './carousel.css';

import image1 from './../../images/empty.png';
import image2 from './../../images/free.png';
import image3 from './../../images/conference.png';

export default class Example extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      images: [image1, image2, image3]
    };
  }

  componentDidMount(e) {
    // setInterval(() => {
    //   if (this.state.images[0] == image1) {
    //     this.setState({images: [image3, image1, image2]});
    //   } else if (this.state.images[0] == image2) {
    //     this.setState({images: [image1, image2, image3]});
    //   } else if (this.state.images[0] == image3) {
    //     this.setState({images: [image2, image3, image1]})
    //   }
    // }, 3000);
  }
  
  render() {
      return (
          <div id="carousel">
            <Col md="12" col-sm="12" xs="12" id="background_carousel">
            {/* <Col md={{size: 10, offset: 1}} sm={{size: 10, offset: 1}} xs={{size: 10, offset: 1}} id="background_carousel"> */}
              <Row>
                <Col md="6" sm="6" xs="6">
                  <img src={this.state.images[0]} />
                </Col>
                <Col md="6" sm="6" xs="6">
                  <img src={this.state.images[1]} />
                </Col>
              </Row>
            </Col>
            <Col md={{size: 8, offset: 2}} sm={{size: 8, offset: 2}} xs={{size: 8, offset: 2}} id="main_carousel">
              <img src={this.state.images[2]} />
            </Col>
          </div>
      );
  }
}


